<template>
	<div class="HelpCenter">
		<!-- :style="scrollHeight" -->
		<p style="color: #a5a5a5;padding: 10px;margin-bottom: 0;">常见问题</p>
		<div class="content" ref="container">
		<div>
		<mu-load-more :loaded-all="loadAll" @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">	
		  <mu-list toggle-nested>
		    <mu-list-item button :ripple="false" nested :open="open === 'send'" @toggle-nested="open = arguments[0] ? 'send' : ''">
		      <!--<mu-list-item-title>如何创建钱包?</mu-list-item-title>-->
		      <p style="width: 100%;font-size: 16px;">如何创建钱包?</p>
		      <mu-list-item-action>
		        <!--<mu-icon class="toggle-icon" size="24" value="keyboard_arrow_down"></mu-icon>-->
		      	<i v-if="open === 'send'" class="iconfont">&#xe63f;</i>
		      	<i v-if="open === ''" class="iconfont">&#xe695;</i>
		      </mu-list-item-action>
		      <mu-list-item style="padding-bottom:8px;" button :ripple="false" slot="nested">
		        <p>打开dylc钱包，在钱包其实也选择船舰钱包，设置交易密码后，钱包创建成功；用户需要根据提示备份好钱包助记词，拥有助记词即可完全控制钱包从资产。</p>
		      </mu-list-item>
		    </mu-list-item>
		    <mu-list-item button :ripple="false" nested :open="open === 'drafts'" @toggle-nested="open = arguments[0] ? 'drafts' : ''">
		      <p style="width: 100%;font-size: 16px;">如何创建钱包如何创建钱包如何创建钱包如何创建钱包</p>
		      <mu-list-item-action>
		        <i v-if="open === 'drafts'" class="iconfont">&#xe63f;</i>
		      	<i v-if="open === ''" class="iconfont">&#xe695;</i>
		      </mu-list-item-action>
		      <mu-list-item button :ripple="false" slot="nested">
		        <mu-list-item-title>List Item 1</mu-list-item-title>
		      </mu-list-item>
		      <mu-list-item button :ripple="false" slot="nested">
		        <mu-list-item-title>List Item 2</mu-list-item-title>
		      </mu-list-item>
		      <mu-list-item button :ripple="false" slot="nested">
		        <mu-list-item-title>List Item 3</mu-list-item-title>
		      </mu-list-item>
		    </mu-list-item>
		    </mu-list>
		  </mu-load-more>
		  
		<!--<p v-if="helpList.length==0" style="text-align: center;">暂无数据...</p>-->
		<!--<p style="text-align: center;" v-show="page>1&&loadAll">没有更多数据啦~~</p>-->
		</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'HelpCenter',
  data(){
  	return{
  		type:'',
  		page:1,
  		open: '',
  		helpList:[],
  		loading:false,
  		loadAll:false,
  		refreshing:false,
  	}
  },
  methods:{
  	getHelpList(){
  		let _this = this;
  		_this.$http.get(_this.$lib.url + 'helping/lists?page='+_this.page).then(res => {
			console.log(res);
			if (res.status == 0){
				_this.refreshing = false;
				const _list = res.data.data;
		        _this.helpList = [...this.helpList, ..._list];
		        if(_list.length < 10){
		            /* 所有数据加载完毕 */
					_this.loading = false;
					_this.loadAll = true;
		            return;
		        }
			}
		}).catch(err => {})
  	},
  	//下拉刷新
	refresh() {
		this.refreshing = true;
		this.loadAll = false;
		this.$refs.container.scrollTop = 0;
		setTimeout(() => {
			this.page=1;
			this.helpList=[];
//			this.getHelpList();
		},1000)
	},
	//加载更多
	load() {
		this.loading = true;
		this.page++;
		setTimeout(() => {
			this.loading = false;//不设置的话第二次上拉加载不会调用load方法
//			this.getHelpList();
		},1000)
	}
  },
  mounted(){
  	this.page=1;
  	this.helpList=[];
//	this.getHelpList();
  },
  computed:{
//	scrollHeight(){
//		return "height:"+((window.screen.height)-56)+"px";
//	}
  },
  watch:{
  	$route(to,from){
  		
  	}
  }
 }
</script>

<style scoped>
.HelpCenter{
	overflow-y: scroll;
}

</style>


